.gallery {
  border-top: 1px solid yellowgreen;
  margin: 5% auto;
  padding: 5%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
  background-color: yellowgreen;
}
img {
  width: 90%;
}
figure {
  background-color: #fff;
  width: 100%;
  height: 100%;
  border: 1px solid #ddd;
  margin: 0 auto;
  border-radius: 10px;
}

figcaption {
  text-align: left;
  font-size: 0.9rem;
}

@media (width >= 500px) {
  .gallery {
    width: 90%;
  }
  img {
    width: 100%;
  }
  figure {
    background-color: #fff;
    width: 100%;
    height: 100%;
    border: 1px solid #ddd;
    margin: 0 auto;
    border-radius: 10px;
  }
}
