button {
  background-color: green;
  color: white;
  font-weight: bold;
}

.search {
  border: 1px solid yellowgreen;
}

input {
  margin: 20px 0;
  padding: 20px 20%;
  border-radius: 50px;
  border: 1px solid yellowgreen;
  font-size: 1.5rem;
  outline: none;
}

a {
  background: url(../../../public/images/NAVER.png) no-repeat 50% / cover;
  position: absolute;
  width: 100px;
  height: 50px;
  z-index: 1;
  top: 4vh;
}
a::after {
  background: url(../../../public/images/papago.svg) no-repeat 0% / cover;
  position: fixed;
  content: "";
  width: 45px;
  height: 50px;
  z-index: 1;
  top: 15vh;
  left: 90vw;
}

button {
  background: url(../../../public//images/search.png) no-repeat;
  height: 40px;
  width: 40px;
  text-indent: -9999px;
  border: none;
  cursor: pointer;
}
